/* @import '~antd/dist/antd.css'; */

.fz-layout-header {
	padding: 0;
	display: flex;
}

.fz-layout-header-menu {
	line-height: 64px;
	flex: 1;
}

.fz-layout-content {
	padding: 16px 0px;
	min-height: 80vh;
}

.fz-layout-footer {
	/* line-height: 1.5; */
	display: flex;
}

.fz-list-page {
	padding: 0 5px;
}

.fz-table-wrapper {
	/* margin-top: 15px; */
	/* padding: 0 50px; */
	background: #fff;
	margin-top: 50px;
	min-height: 280px;
}

.fz-table {
	background: #fff;
}

.fz-table .ant-table-body {
	overflow: auto;
}

.fz-logo {
	width: 120px;
	height: 31px;
	background: rgba(255, 255, 255, 0.2);
	margin: 16px 0 16px 0;
	/* float: left; */
}

.fcb-find-us {
	display: block;
	width: 98px;
	height: 18px;
	background: url('/static/img/fcb-find-us.png');
}

/* .fz-btn-group {
	padding: 16px 5px;
} */

.fz-btn-group .ant-btn {
	margin-right: 8px;
}

.fz-loading-overlay_overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0px;
    left: 0px;
    display: flex;
    text-align: center;
    font-size: 1.2em;
    color: #A9A9A9;
	z-index: 800;
	opacity: 1;
	background: rgba(255, 255, 255, 0.8);
	transition: none;
}

.fz-loading-overlay_content {
	position: fixed;
	top: 50%;
	left: 50%;
	margin-top: -25px;
	margin-left: -50px;
}

.fz-dynamic-delete-button {
	color: #1088e9;
}

.fz-footer-brand {
	flex-grow: 1;
	line-height: 17px;
}

.fz-back-top {
	bottom: 165px;
	right: 0px;
}
.fz-back-top-inner {
	height: 40px;
	width: 40px;
	line-height: 40px;
	border-radius: 4px;
	background-color: #1088e9;
	color: #fff;
	text-align: center;
	font-size: 20px;
}

@media only screen and (min-width: 992px) {
	.fz-layout-header {
		padding: 0 24px;
	}
	.fz-layout-content {
		/* padding-left: 5vw;
		padding-right: 5vw; */
		/* padding: 0 50px; */
		padding: 24px;
	}
	/* .fz-btn-group {
		padding: 0;
	} */
	.fz-table-wrapper {
		padding: 24px;
	}
}
